import uniIcon from '@/components/uni-icons/uni-icons.vue'
import linkUrl from "@/components/link/link.vue"
import contact from "@/components/contact/contact.vue"
import banner from "@/components/banner/banner.vue"
export default {
	components:{uniIcon,linkUrl,banner,contact},
	data() {
		return {
			title: 'Hello',
			collectionI:false,
			collectionT:'收藏',
			qrcode:'',
			companyInformation:null,
			virtualPhone:false
		}
	},
	onShow(){
		this.getQcode();
	},
	onLoad(option) {
		this.qrcode = option.qrcode;
		
	},
	methods: {
		checkVirtualPhone(){
			let _this = this;
			this.$ajax("/my/hasVirtualService","POST",{phone:_this.companyInformation.realPhone}).then((res)=>{
				this.companyInformation.virtualPhone = res;
			})	
		},
		toMine(){
			uni.navigateTo({
				"url":"/pages/address/msdh/dhm1?qcode="+this.qrcode
			})
			
		},
		buyVirtualPhone(){
			let _this = this;
			if(_this.companyInformation.useVirtualPhone == 0){
				uni.navigateTo({
					"url":"/pages/phone/contactway?phone="+_this.companyInformation.realPhone
				})
			}else{
				uni.showModal({
					showCancel:false,
					title:"虚拟电话",
					content:"此电话已经开通了虚拟电话，他人扫码将无法获取你的真实号码",
					success:function(res){
						if(res.confirm){
							
						}
					}
				})
			}
			
		},
		getQcode(){
			let _this = this;
			
			this.$ajax("/nav/detail","POST",{qrcodeNo:_this.qrcode}).then((res)=>{
				_this.companyInformation = res.detail;
				console.log(_this.companyInformation);
				
				_this.checkVirtualPhone();
				if(_this.companyInformation.virtualPhone){
					uni.showModal({
						showCancel:false,
						title:"虚拟电话",
						content:"此电话已经开通了虚拟电话，他人扫码将无法获取你的真实号码",
						success:function(res){
							if(res.confirm){
								
							}
						}
					})
				}else{
					
				}
			})
			
			
			
		},
		preview(i,type){
			let _this = this;
			var url = "";
			if(type == 0){
				
				url = _this.companyInformation.realImgs
			}else{
				url = _this.companyInformation.innerImgs
			}
			uni.previewImage({
				urls: url
			});
		},
		collection(){
			if(this.collectionI){
				this.collectionI = !this.collectionI;
				this.collectionT = '收藏'
				uni.showToast({
					"title":"收藏成功"
				})
			}else{
				this.collectionI = !this.collectionI;
				
				this.collectionT = '取消'
				uni.showToast({
					"title":"取消收藏成功"
				})
			}
			
			
		},
		toUrl(url){
			uni.navigateTo({
				"url":url
			})
		}
	}
}
